  body::before {
    content: "";
    position: fixed;
    inset: 0;
    z-index: -2;
    pointer-events: none;
    background: 
      /* Sophisticated gradient base - VERY SUBTLE */
      linear-gradient(135deg, 
        rgba(255, 140, 0, 0.02) 0%, 
        rgba(255, 165, 0, 0.01) 50%, 
        rgba(255, 140, 0, 0.03) 100%
      ),
      /* Large elegant circles - like sun/moon - SUBTLE */
      radial-gradient(circle at 80% 20%, 
        rgba(255, 140, 0, 0.05) 0%, 
        rgba(255, 165, 0, 0.03) 25%, 
        transparent 50%
      ),
      radial-gradient(circle at 20% 80%, 
        rgba(255, 165, 0, 0.04) 0%, 
        rgba(255, 140, 0, 0.02) 25%, 
        transparent 45%
      ),
      /* Ambient glow - VERY SUBTLE */
      radial-gradient(circle at 50% 50%, 
        transparent 0%, 
        rgba(255, 140, 0, 0.015) 100%
      );
    background-size: 100% 100%, 800px 800px, 700px 700px, 100% 100%;
  }
  
  /* Dark mode - elegant gradient design */
  .dark body::before {
    background: 
      /* Sophisticated gradient base for dark */
      linear-gradient(135deg, 
        rgba(255, 165, 0, 0.03) 0%, 
        transparent 50%, 
        rgba(255, 140, 0, 0.04) 100%
      ),
      /* Large elegant circles with better contrast */
      radial-gradient(circle at 80% 20%, 
        rgba(255, 165, 0, 0.12) 0%, 
        rgba(255, 140, 0, 0.06) 25%, 
        transparent 50%
      ),
      radial-gradient(circle at 20% 80%, 
        rgba(255, 140, 0, 0.10) 0%, 
        rgba(255, 165, 0, 0.05) 25%, 
        transparent 45%
      ),
      /* Subtle ambient glow */
      radial-gradient(circle at 50% 50%, 
        transparent 0%, 
        rgba(255, 165, 0, 0.03) 100%
      );
  }
  
  /* Japanese wave pattern (Seigaiha) - SUBTLE IN LIGHT MODE */
  body::after {
    content: "";
    position: fixed;
    inset: 0;
    z-index: -1;
    pointer-events: none;
    opacity: 0.5;
    background-image: 
      repeating-radial-gradient(
        circle at 0% 50%,
        transparent 0,
        transparent 20px,
        rgba(255, 140, 0, 0.03) 20px,
        rgba(255, 140, 0, 0.03) 21px,
        transparent 21px,
        transparent 40px
