        rgba(255, 140, 0, 0.02) 20px,
        rgba(255, 140, 0, 0.02) 21px,
        transparent 21px,
        transparent 40px
      );
    background-size: 60px 120px, 60px 120px;
    background-position: 0 0, 30px 60px;
  }
  
  .dark body::after {
