/* Elegant geometric Japanese pattern overlay - SUBTLE IN LIGHT MODE */
.japanese-pattern {
  position: fixed;
  inset: 0;
  z-index: -1;
  pointer-events: none;
  opacity: 0.05;
  background-image: 
    linear-gradient(30deg, transparent 40%, rgba(255, 140, 0, 0.1) 40%, rgba(255, 140, 0, 0.1) 60%, transparent 60%),
    linear-gradient(60deg, transparent 40%, rgba(255, 140, 0, 0.08) 40%, rgba(255, 140, 0, 0.08) 60%, transparent 60%),
    linear-gradient(90deg, transparent 40%, rgba(255, 140, 0, 0.05) 40%, rgba(255, 140, 0, 0.05) 60%, transparent 60%);
