/* Subtle decorative elements */
.decorative-corner {
  position: fixed;
  width: 200px;
  height: 200px;
  pointer-events: none;
  z-index: -1;
}

.decorative-corner::before {
  content: "";
  position: absolute;
  inset: 0;
  border: 1px solid rgba(255, 140, 0, 0.06);
  border-radius: 50%;
  transform: scale(1.5);
}

.decorative-corner::after {
  content: "";
  position: absolute;
  inset: 20px;
  border: 1px solid rgba(255, 140, 0, 0.04);
  border-radius: 50%;
}

.decorative-corner.top-left {
  top: -100px;
  left: -100px;
}

.decorative-corner.top-right {
  top: -100px;
  right: -100px;
}

.decorative-corner.bottom-left {
  bottom: -100px;
  left: -100px;
}

.decorative-corner.bottom-right {
  bottom: -100px;
  right: -100px;
}

.dark .decorative-corner::before {
  border-color: rgba(255, 165, 0, 0.12);
}

.dark .decorative-corner::after {
  border-color: rgba(255, 165, 0, 0.08);
